/* Wrapper for Navbar */
.NavBarWrapper {
  padding-block: 10px;
  display: flex;
  flex-direction: column;
  background-color: #2c3e50;
  /* Set the desired background color */
}

/* Navbar Styling */
/* Navbar Styling */
.Navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  /* Standard navbar height */
  background-color: #2c3e50;
  /* Dark background */
  color: whitesmoke;
  /* Light text color */
  display: flex;
  align-items: center;
  /* Center items vertically */
  justify-content: center;
  /* Center the title horizontally */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  /* Subtle shadow for depth */
  z-index: 1000;
  /* Ensure it stays above other elements */
  margin-inline: 10px;
}

/* Navbar Title */
.NavBarTitleText {
  margin: 0;
  font-size: 1.8rem;
  /* Large title font */
  font-family: "Arial", sans-serif;
  /* Clean font */
  text-align: center;
  color: whitesmoke;
  /* Light text color */
}


/* Theme Toggle Button */
.ThemeToggle {
  all: unset;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: 1px solid #3498db;
  /* Accent color */
  border-radius: 8px;
  background-color: #34495e;
  /* Slightly lighter than navbar */
  padding: 5px 15px;
  /* Adjust padding */
  color: whitesmoke;
  /* Light text */
  transition: background-color 0.3s ease, color 0.3s ease;
}

.ThemeToggle:hover {
  background-color: #2980b9;
  /* Hover background color */
  color: white;
  /* Text color on hover */
}

/* Theme Icon */
.ThemeIcon {
  transition: all 0.3s ease;
  margin-left: 8px;
  font-size: 20px;
  /* Smaller icon size */
}